.checkbox {
    display: inline-flex;
    cursor: pointer;
    position: relative;
}

.checkbox > span {
    color: black;
    padding: 0.5rem 0.25rem;
}

.checkbox > input {
    height: 25px;
    width: 25px;
    -webkit-appearance: none;
    -o-appearance: none;
    appearance: none;
    border: 1px solid #738678;
    border-radius: 4px;
    outline: none;
    transition-duration: 0.3s;
    background-color: transparent;
    cursor: pointer;
  }
  .checkbox > input:hover {
      background-color: #738678;
  }

.checkbox > input:checked {
    border: 1px solid #738678;
    background-color: #867381;
    
}
.checkbox>input:checked + label {
    content: √;
}

.checkbox > input:checked + span::before {
    content: '\2713';
    display: block;
    text-align: center;
    color: #738678;
    position: absolute;
    left: 0.7rem;
    top: 0.2rem;
}

.checkbox > input:active {
    border: 2px solid #867381;
}

